<template>
	<div>
		<div class="pricing-free-trial">
			<b-row>
				<b-col lg="10" offset-lg="3" class="mx-auto">
					<div class="pricing-trial-content d-flex justify-content-between">
						<div class="text-center text-md-left mt-3">
							<h3 class="text-primary">
								用户通过你的链接注册，你将获得他所支付的 15% 的金额。
							</h3>
							<b-button @click="generateInviteCode()" v-show="message1 == ''" variant="primary" class="mt-2 mt-lg-3">
								生成订阅链接
							</b-button>
							<b-input-group class="mt-5" v-show="message1 != ''">
								<b-form-input v-model="message1" />
								<b-input-group-append>
									<b-button variant="primary" @click="copyLink(message1)">
										复制
									</b-button>
								</b-input-group-append>
							</b-input-group>

							<!-- button -->
						</div>

						<!-- images -->
						<b-img fluid :src="require('@/assets/images/illustration/pricing-Illustration.svg')" class="pricing-trial-img" alt="svg img" />
						<!--/ images -->
					</div>
				</b-col>
			</b-row>
		</div>
		<b-card>
			<b-card-header>
				<div class="d-flex align-items-center">
					<b-card-title class="ml-25">
						邀请记录
					</b-card-title>
				</div>
			</b-card-header>
			<b-table responsive :items="items" :fields="fields" class="mb-0">
				<template #cell(commission_status)="data">
					<b-badge pill :variant="data.value == 2 ? 'success' : 'danger'">
						{{ data.value == 2 ? '已发放' : '待确认' }}
					</b-badge>
				</template>
				<template #cell(commission_balance)="data"> ￥{{ data.value / 100 }} </template>
				<template #cell(created_at)="data">
					{{ data.value | formatDate }}
				</template>
				<template #cell(updated_at)="data">
					{{ data.value | formatDate }}
				</template>
				<!-- Optional default data cell scoped slot -->
				<template #cell()="data">
					{{ data.value }}
				</template>
			</b-table>
		</b-card>
	</div>
</template>

<script>
import {
	BCardHeader,
	BInputGroupAppend,
	BRow,
	BCol,
	BFormGroup,
	BFormInput,
	BCard,
	BCardTitle,
	BFormTextarea,
	BCardText,
	BButton,
	BTable,
	BBadge,
	BImg,
	BInputGroupPrepend,
	BInputGroup
} from 'bootstrap-vue'
import { formatDate } from '@/libs/date'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'

export default {
	data() {
		return {
			fields: [
				{ key: 'id', label: 'id' },
				{ key: 'commission_balance', label: '佣金' },
				{ key: 'commission_status', label: '佣金状态' },
				{ key: 'created_at', label: '创建时间' },
				{ key: 'updated_at', label: '更新时间' }
			],
			items: [],
			isPosting: true,
			inviteInfo: {
				codes: [
					{
						code: 0
					}
				],
				stat: [0, 0, 0, 0, 0]
			},
			inviteDetials: [],
			userInfo: {},
			gettingUserInfo: true,
			message1: ''
		}
	},
	components: {
		BCardHeader,
		BFormGroup,
		BFormInput,
		BCard,
		BCardTitle,
		BFormTextarea,
		BCardText,
		BButton,
		BTable,
		BBadge,
		BImg,
		BRow,
		BCol,
		BInputGroup,
		ToastificationContent,
		BInputGroupAppend,
		BInputGroupPrepend
	},
	filters: {
		formatDate(time) {
			time = time * 1000
			var date = new Date(time)
			return formatDate(date, 'yyyy-MM-dd hh:mm')
		}
	},
	mounted() {
		this.getInviteInfo()
		this.getInviteDetails()
		this.getUserInfo()
	},
	methods: {
		generateInviteCode() {
			this.$http({
				url: '/api/v1/user/invite/save',
				method: 'GET'
			})
				.then(res => {
					res = res.data.data

					if (res == true) {
						//   this.$store.commit("setUserSub", res);
						this.getInviteInfo()
					}
					this.isPosting = false
				})
				.catch(err => {
					if (err.response.data.message) {
						msg(err.response.data.message)
					}
					this.isPosting = false
				})
		},
		getInviteInfo() {
			this.isPosting = true

			this.$http({
				url: '/api/v1/user/invite/fetch',
				method: 'GET'
			})
				.then(res => {
					res = res.data

					if (res) {
						//   this.$store.commit("setUserSub", res);
						this.inviteInfo = res.data
						console.log(res)
						this.message1 = res.data.codes.length > 0 ? 'https://www.ecosayss.com/register/' + res.data.codes[0].code : ''
					}
					this.isPosting = false
				})
				.catch(err => {
					if (err.response && err.response.data.message) {
						this.$cookies.set('isLogin', 0)
						this.$router.push({
							name: 'Login'
						})
					}
					this.isPosting = false
				})
		},
		generateInviteCode() {
			this.$http({
				url: '/api/v1/user/invite/save',
				method: 'GET'
			})
				.then(res => {
					res = res.data.data

					if (res == true) {
						//   this.$store.commit("setUserSub", res);
						this.getInviteInfo()
					}
					this.isPosting = false
				})
				.catch(err => {
					if (err.response.data.message) {
						msg(err.response.data.message)
					}
					this.isPosting = false
				})
		},
		getInviteDetails() {
			this.isPosting = true

			this.$http({
				url: '/api/v1/user/invite/details',
				method: 'GET'
			})
				.then(res => {
					res = res.data

					if (res) {
						//   this.$store.commit("setUserSub", res);
						this.items = res.data
					}
					this.isPosting = false
				})
				.catch(err => {
					if (err.response.data.message) {
						this.$cookies.set('isLogin', 0)
						this.$router.push({
							name: 'Login'
						})
					}
					this.isPosting = false
				})
		},

		getUserInfo() {
			this.gettingUserInfo = true

			this.$http({
				url: '/api/v1/user/info',
				method: 'GET'
			})
				.then(res => {
					res = res.data

					if (res) {
						//   this.$store.commit("setUserSub", res);
						this.userInfo = res.data
					}
					this.gettingUserInfo = false
				})
				.catch(err => {
					if (err.response.data.message) {
						this.$cookies.set('isLogin', 0)
						this.$router.push({
							name: 'Login'
						})
					}
					this.gettingUserInfo = false
				})
		},
		copyLink(link) {
			var aux = document.createElement('input')
			aux.setAttribute('value', link)
			document.body.appendChild(aux)
			aux.select()
			document.execCommand('copy')
			document.body.removeChild(aux)
			this.$toast({
				component: ToastificationContent,
				props: {
					title: '复制成功',
					icon: 'BellIcon'
				}
			})
		}
	}
}
</script>
